.get-price {
	padding: 77px 0;
	
	&_title {
		max-width: 45%;
		font-weight: bold;
		font-size: 40px;
		line-height: 115%;
		text-align: center;
		color: #333333;
		margin: 0 auto 17px auto;
	}
	
	&_text {
		max-width: 55%;
		font-weight: 600;
		font-size: 20px;
		line-height: 27px;
		text-align: center;
		color: #9A9A9A;
		margin-bottom: 30px;
		margin: 0 auto 25px auto;
	}
	
	&_choose-type {
		text-align: center;
		font-weight: 600;
		font-size: 20px;
		line-height: 27px;
		color: #333333;
		margin-bottom: 12px;
	}
	
	&_choose-type-radio {
		display: flex;
		justify-content: center;
		margin-bottom: 30px;
		
		.radio-group {
			margin-right: 33px;
			
			&:last-child {
				margin-right: 0;
			}
		}
	}
	
	&_input-group {
		margin-bottom: 20px;
	}
	
	&_input {
		display: block;
		width: 500px;
		margin: 0 auto;
		font-weight: 600;
		font-size: 20px;
		line-height: 30px;
		padding: 24px 36px;
		background: #F7F7F7;
		margin-bottom: 15px;
		outline: none;
		border: none;
		
		&::-webkit-input-placeholder {color: #333333;opacity: 0.7;}
		&::-moz-placeholder          {color: #333333;opacity: 0.7;}
		&:-moz-placeholder           {color: #333333;opacity: 0.7;}
		&:-ms-input-placeholder      {color: #333333;opacity: 0.7;}
		
		&:last-child {
			margin-bottom: 0;
		}
	}
	
	&_button {
		display: block;
		width: 500px;
		font-weight: bold;
		font-size: 22px;
		line-height: 30px;
		margin: 0 auto;
		padding: 25px 0;
		text-align: center;
		margin-bottom: 17px;
	}
	
	&_confidience {
		display: block;
		max-width: 420px;
		margin: 0 auto;
		font-weight: 500;
		font-size: 17px;
		line-height: 23px;
		text-align: center;
		opacity: 0.7;
		
		a {
			color: $blue;
			
			&:hover {
				cursor: pointer;
				color: $blue-hover;
				opacity: 1;
			}
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $lg) {

	.get-price {
		padding: 77px 0;
		
		&_title {
			max-width: 65%;
		}
		
		&_text {
			max-width: 75%;
		}
	}
	
}

@media (max-width: $md) {

	
}

@media (max-width: $sm) {

	.get-price {
		padding: 57px 0 72px 0;
		
		&_title {
			max-width: 90%;
			font-size: 26px;
			line-height: 32px;
			margin: 0 auto 12px auto;
		}
		
		&_text {
			max-width: 100%;
			font-size: 16px;
			line-height: 23px;
			margin: 0 0 16px 0;
		}
		
		&_choose-type {
			font-size: 16px;
			line-height: 27px;
			margin-bottom: 6px;
		}
		
		&_choose-type-radio {
			display: flex;
			justify-content: center;
			margin-bottom: 20px;
			
			.radio-group {
				margin-right: 43px;
				
				&:last-child {
					margin-right: 0;
				}
			}
		}
		
		&_input-group {
			margin-bottom: 12px;
		}
		
		&_input {
			width: 100%;
			font-size: 16px;
			padding: 15px 22px;
			margin-bottom: 10px;
			
			&:last-child {
				margin-bottom: 0;
			}
		}
		
		&_button {
			width: 100%;
			font-size: 16px;
			margin: 0 auto;
			padding: 15px 0;
			text-align: center;
			margin-bottom: 7px;
		}
		
		&_confidience {
			display: block;
			max-width: 90%;
			margin: 0 auto;
			font-weight: 500;
			font-size: 14px;
			line-height: 21px;
			text-align: center;
			opacity: 0.7;
			
			a {
				color: $blue;
				text-decoration: underline;
				
				&:hover {
					cursor: pointer;
					color: $blue-hover;
					opacity: 1;
				}
			}
		}
	}
}