.cases {
	
	&_title {
		margin-bottom: 18px;
	}
	
	&_tags {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 46px;
	}
	
	&_tag {
		font-size: 18px;
		line-height: 22px;
		color: #3B56D7;
		background: #E5F1FF;
		border-radius: 5px;
		padding: 5px 14px;
		transition: 200ms;
		margin-right: 20px;
		
		&:hover {
			cursor: pointer;
			color: #FFFFFF;
			background: #3B56D7;
		}
	}
	
	&_cards {
		margin-bottom: 45px;
	}
	
	&_card {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		position: relative;
		background: #FFFFFF;
		box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.05);
		border-radius: 8px;
		padding: 23px 31px 32px 25px;
		margin-bottom: 20px;
		
		&:last-child {
			margin-bottom: 0;
		}
	}
	
	&_card-tag {
		position: absolute;
		top: 29px;
		right: 31px;
		font-size: 18px;
		line-height: 22px;
		color: #3B56D7;
		background: #E5F1FF;
		border-radius: 5px;
		padding: 5px 14px;
		transition: 200ms;
		
		&:hover {
			cursor: pointer;
			color: #FFFFFF;
			background: #3B56D7;
		}
	}
	
	&_card-image {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 400px;
		min-width: 400px;
		height: 280px;
		margin-right: 24px;
		
		&.empty {
			border: 1px solid #CCCCCC;
		}
		
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
		
		svg {
			margin-bottom: 9px;
		}
		
		p {
			font-size: 16px;
			line-height: 19px;
			color: #A4A4A4;
		}
	}
	
	&_card-info {
		width: calc(100% - 424px);
	}
	
	&_card-title {
		font-size: 24px;
		line-height: 29px;
		color: #3B56D7;
		margin-bottom: 3px;
	}
	
	&_card-descrition {
		font-size: 20px;
		line-height: 24px;
		color: #8C8C8C;
		margin-bottom: 18px;
	}
	
	&_card-slider {
		width: 100%;
		border: 1px solid #DEDEDE;
		box-sizing: border-box;
		border-radius: 6px;
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: nowrap;
		padding: 17px 30px 17px 19px;
		margin-bottom: 25px;
	}
	
	&_card-slider-item {
		width: 15.5%;
		margin-right: 5.625%;
		
		&:last-child {
			margin-right: 0;
		}
		
		div {
			font-weight: bold;
			font-size: 30px;
			line-height: 37px;
			color: #3C3C3C;
			margin-bottom: 5px;
		}
		
		p {
			font-size: 18px;
			line-height: 22px;
			color: #8C8C8C;
		}
	}
	
	&_card-button {
		display: inline-block;
		width: 260px;
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		padding: 16px 0 14px;
	}
	
	&_show-more {
		display: block;
		align-items: center;
		width: 250px;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		text-align: center;
		padding: 19px 0;
		margin: 0 auto;
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $lg) {

	.cases {
		
		&_card-slider-item {
			width: 15.5%;
			margin-right: 5.625%;
			
			&:last-child {
				margin-right: 0;
			}
			
			div {
				font-weight: bold;
				font-size: 24px;
				line-height: 30px;
			}
			
			p {
				font-size: 16px;
				line-height: 20px;
			}
		}
		
		&_card-tag {
			top: 15px;
			right: 15px;
		}
	}
}

@media (max-width: $md) {

}

@media (max-width: $sm) {

	.cases {
	
		&_title {
			margin-bottom: 21px;
		}
		
		&_tags {
			margin-bottom: 16px;
		}
		
		&_tag {
			padding: 7px 9px 4px 9px;
			margin-right: 10px;
			margin-bottom: 10px;
			font-size: 16px;
			line-height: 19px;
		}
		
		&_cards {
			margin-bottom: 30px;
		}
		
		&_card {
			display: block;
			margin-bottom: 20px;
			padding: 16px 11px 13px 12px;
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
			
			&:last-child {
				margin-bottom: 0;
			}
		}
		
		&_card-tag {
			position: static;
			display: inline-block;
			height: 26px;
			font-size: 14px;
			padding: 2px 9px 6px 9px;
			margin: 0;
		}
		
		&_card-image {
			width: 100%;
			min-width: 100%;
			height: auto;
			margin-bottom: 12px;
			
			&.empty {
				height: 187px;
			}
			
			img {
				
			}
			
			svg {
				width: 40px;
				height: 40px;
				margin-bottom: 4px;
			}
			
			p {
				font-size: 14px;
				line-height: 17px;
			}
		}
		
		&_card-info {
			width: 100%;
		}
		
		&_mobile-title-box {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 8px;
		}
		
		&_card-title {
			position: relative;
			top: 2px;
			width: 100%;
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 0px;
		}
		
		&_card-descrition {
			width: 100%;
			font-size: 16px;
			line-height: 19px;
			margin-bottom: 11px;
		}
		
		&_card-slider {
			position: relative;
			border: 1px solid #DEDEDE;
			box-sizing: border-box;
			border-radius: 6px;
			display: block;
			padding: 21px 0 20px 0;
			margin-bottom: 17px;
			
			&.slick-dotted {
				margin-bottom: 43px;
			}
			
			.slick-list {
				width: 100%;
			}
			
			.slick-track {
				// padding-left: 20px;
			}
			
			.slick-slide {
				padding-left: 15px;
				padding-right: 10px;
				// padding-right: 15px;
			}
			
			.slick-dots {
				position: absolute;
				display: block;
				left: 0;
				width: 100%;
				bottom: -23px;
				display: flex;
				align-items: flex-start;
				justify-content: center;
				
				li {
					width: 8px;
					height: 8px;
					margin-right: 12px;
					
					&:last-child {
						margin-right: 0;
					}
					
					&.slick-active button {
						background: rgb(196, 196, 196);
					}
				}
				
				li button {
					display: inline-block;
					vertical-align: top;
					width: 8px;
					height: 8px;
					font-size: 0;
					border-radius: 50%;
					background: rgba(196, 196, 196, 0.5);
					padding: 0;
					
					&.slick-active {
						background: rgb(196, 196, 196);
					}
				}
			}
		}
		
		&_card-slider-item {
			// padding: 0 28px 0 22px;
			
			&:last-child {
				margin-right: 0;
			}
			
			div {
				font-size: 24px;
				line-height: 29px;
				margin-bottom: 0;
			}
			
			p {
				max-width: 90%;
				font-size: 16px;
				line-height: 18px
			}
		}
		
		&_card-button {
			height: 40px;
			font-size: 18px;
			line-height: 22px;
			padding: 7px 0 8px 0;
			border-radius: 4px;
		}
		
		&_show-more {
			width: 100%;
			font-size: 20px;
			line-height: 24px;
			text-align: center;
			padding: 8px 0 8px 0;
			margin: 0;
		}
	}
}