input.ios-radio {
  position: absolute;
  opacity: 0; }

input.ios-radio + label {
  position: relative;
  bottom: 2px;
  display: block;
  padding-left: 32px;
  font-size: 22px;
  height: 28px;
  line-height: 28px;
  color: #333333;
  cursor: pointer;
  position: relative; }

input.ios-radio:checked + label {
  color: #00CA69; }

label.ios-radio-label:after {
  content: '';
  position: absolute;
  z-index: 104;
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
  top: 8px;
  left: 6px;
  transition: all .3s; }

label.ios-radio-label:before {
  content: '';
  left: 0px;
  position: absolute;
  top: 2px;
  z-index: 100;
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #fff;
  border: #333333 solid 2px;
  opacity: 0.9; }

input.ios-radio[type="radio"]:checked + label:after {
  background-color: #4AD963; }

input.ios-radio[type="radio"]:checked + label::before {
  border: #4AD963 solid 2px; }

@media (max-width: 767px) {
  input.ios-radio + label {
