@media (max-width: $sm) {
	
	input.ios-radio + label {
		position: relative;
		bottom: 2px;
		display:  block;
		padding-left: 32px;
		font-size: 18px;
		cursor: pointer;
		position: relative;
	}
	
	label.ios-radio-label:after {
		content: "";
		top: 10px;
	}
	
	label.ios-radio-label:before {
		content: '';
		top: 4px;
	}
}