.breadcrumbs {
    font-size: 18px;
    line-height: 22px;
    padding-top: 33px;
	margin-bottom: 18px;
	
	@media (max-width: 540px) {
		font-size: 16px;
		line-height: 19px;
		padding-top: 21px;
		margin-bottom: 6px;
	}
	
	.back-arrow {
		display: none;
		
		@media (max-width: 540px) {
			display: inline-block;
			margin-right: 8px;
		}
		
		svg {
			display: inline-block;
		}
	}
	
	.breadcrubs-group {
		display: inline-block;
	}
	
	a {
		display: inline-block;
		position: relative;
		margin-left: 10px;
		color: $black;
		
		@media (max-width: 540px) {
			display: none;
		}
		
		&:after {
			content: "/";
			position: absolute;
			left: -10px;
			color: $black;
			
			@media (max-width: 540px) {
				left: -11px;
			}
			
			&:hover {
				cursor: default;
			}
		}
		
		&:first-child {
			margin-left: 0;
			
			@media (max-width: 540px) {
				display: inline-block;
				margin-left: 0;
			}
			
			&:after {
				content: "";
			}
		}
		
		&:last-child {
			opacity: 0.6;
			
			&:after {
				content: "/";
				opacity: 1;
				
				@media (max-width: 540px) {
					display: none;
				}
			}
		}
	}
}