.Button {
    background: $light_green;
    color: #FFFFFF;
    
    &:hover {
        cursor: pointer;
        background: $button-hover;
    }
    
    &:active {
        background: $button-active;
    }
    
    &__white {
        border: 1px solid $light_green;
        box-sizing: border-box;
        border-radius: 4px;
        color: $light_green;
    }
}